.progress-title {
    background-color: rgb(148, 185, 148);
    padding-right: 15px;
    padding-left: 15px;
    color: white;
}

.w3-color {
    background-color:#7C9C7C;
    height: 100%;
    border-radius: inherit;
    -webkit-transition: width .2s ease-in;
    -o-transition: width .2s ease-in;
    transition: width .2s ease-in;
    color: white;
}


